import React, { useEffect, useState } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Input,
  ModalHeader,
  ModalBody,
  Modal,
  Row,
} from "reactstrap";
import Classes from "./allmedia.module.css";
import axios from "axios";
import Spinner from "reactstrap/lib/Spinner";
import { toast } from "react-toastify";

const Allmedia = (props) => {
  const [images, setImage] = useState([]);
  const [arrayHolder, setArrayHolder] = useState([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState("");
  const [success, setSuccess] = useState(false);
  const [items, setItems] = useState([]);
  const [status, setStatus] = useState(false)

  const filterSearch = (event) => {
    setSearch(event.target.value);
    const value = arrayHolder.filter((item) => {
      const itemData = item.name.toLowerCase();
      const searchData = event.target.value.toLowerCase();
      return itemData.indexOf(searchData) > -1;
    });
    setImage(value);
  };

  const toggleSuccess = () => {
    setSuccess(!success);
  };

  const setChange = (item) => {
    setSuccess(true);
    setItems(item);
  };

  useEffect(() => {
    setLoading(true);
    setStatus(true)
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
            query getAllMultimedia($page:Int,$limit:Int){
                getAllMultimedia(page :$page , limit :$limit){
                  _id,
                  name,
                  dir,
                  format,
                  dimWidth,
                  dimHeight,
                  createdAt
                }
              },
            `,
        variables: {
          page: 1,
          limit: 50,
        },
      },
    })
      .then((res) => {
        const { getAllMultimedia } = res.data.data;
        setImage(getAllMultimedia);
        setArrayHolder(getAllMultimedia);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);


  return (
    <div className="animated fadeIn">
      <Card>
        <CardHeader>
          <Col xs="7">
            <InputGroup>
              <InputGroupAddon addonType="append">
                <InputGroupText>جستجو تصویر</InputGroupText>
              </InputGroupAddon>
              <Input
                placeholder="جستجو کنید ....."
                value={search}
                onChange={filterSearch}
              />
            </InputGroup>
          </Col>
        </CardHeader>
        <CardBody>
          <div className={Classes.AllMediaSection}>
            {loading ? (
              <Spinner animation="grow" size="sm" />
            ) : images == null ? (
              "هیچ تصویری یافت نشد"
            ) : (
              images.map((item) => (
                <div
                  className={Classes.mediaSection}
                  key={item._id}
                  onClick={() => setChange(item)}
                >
                  <img
                    src={`${process.env.REACT_APP_BASE_URL}${item.dir}`}
                    alt={item.dir}
                  />
                </div>
              ))
            )}
          </div>
        </CardBody>
      </Card>
      {items ? (
        <Modal
          isOpen={success}
          toggle={toggleSuccess}
          className={"modal-success " + props.className}
        >
          <ModalHeader toggle={toggleSuccess}>مشخصات محتوای تصویر</ModalHeader>
          <ModalBody>
            <Row>
              <Col xs="4">
                <div className={Classes.BoxImage}>
                  <img
                    src={`${process.env.REACT_APP_BASE_URL}${items.dir}`}
                    alt={items.dir}
                    style={{ width: "100%", height: "60%" }}
                  />
                </div>
              </Col>
              <Col xs="8">
                <Row>
                  <Col xs="6">نام فایل:</Col>
                  <Col xs="6">{items.name}</Col>
                </Row>
                <hr />
                <Row>
                  <Col xs="6">فرمت فایل:</Col>
                  <Col xs="6">{items.format}</Col>
                </Row>
                <hr />
                <Row>
                  <Col xs="6">اندازه طول:</Col>
                  <Col xs="6">{items.dimWidth}px</Col>
                </Row>
                <hr />
                <Row>
                  <Col xs="6">اندازه ارتفاع:</Col>
                  <Col xs="6">{items.dimHeight}px</Col>
                </Row>
                <hr />
                <Row>
                  <Col xs="2">مسیر:</Col>
                  <Col xs="10">{items.dir}</Col>
                </Row>
                <hr />
                <Row>
                  <Col xs="6">تاریخ:</Col>
                  <Col xs="6">
                    {new Date(items.createdAt).toLocaleDateString("fa-IR")}
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
        </Modal>
      ) : null}
    </div>
  );
};

export default Allmedia;
